nav {
  position: fixed;
  display: grid;
  grid-template-columns: 2fr 1fr;
  background: #ffffff;
  width: 80%;
  z-index: 99;
  margin: 0 auto;
  padding: 20px 0;
  box-shadow: 0px 10px 54px -11px rgba(0,0,0,0.75);
}

nav ul {
  width: 100%;
  display: flex;
  list-style: none;
  align-items: center;
  padding: 0;
  margin: 0; 
}

nav li {
  margin: 0 100px 0 0; 
  font-size: 1.2rem;
}

nav ul:first-of-type {
  grid-template-columns: 1fr;
}

nav ul:first-of-type li {
  font-size: 2.2rem;
  font-weight: bold;
  text-indent: 30px;
}

nav ul:first-of-type li span {
  font-weight: 500;
  color: #444444;
}

nav ul:nth-last-of-type {
  grid-template-columns: repeat(3, 1fr);
}

nav ul li a {
  color: #111111;
  text-decoration: none;
  font-weight: bold;
  display: block;
}

nav ul li a:hover {
  color: rgb(255, 150, 0);
}

.navigation {
  text-align: center;
}
header {
  background: url(/react-landing-page/static/media/background.cdc8455b.jpeg);
  background-size: cover;
  background-position: center;
  /* position: relative; */
  /* height: 700px; */
  width: 100%;
  height: auto;
}

header div.top {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 0 auto;
  z-index: 2;
  width: 80%;
  color: #222222;
  padding-top: 16%;
  padding-bottom: 16%;
  align-items: center;
}

header div.top h1 {
  font-size: 5rem;
  font-weight: bold;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 5px;
  /* border-right: 2px solid #111111; */

}

header div.top p {
  padding-left: 30px;
}

.contact {
  background: rgb(255, 150, 0);
  color: #ffffff;
  font-weight: bold;
  display: block;
  margin: 0 auto;
  padding: 12px;
  text-align: center;
  text-decoration: none;
  width: 50%;
  border-radius: 10px;
  box-shadow: 0px 10px 54px -11px rgba(0,0,0,0.75);
}
footer {
  background: rgb(255, 150, 0);
  color: #ffffff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  align-items: center;
  padding: 10px;
  margin-top: 80px;
}

footer ul {
  list-style: none;
  justify-items: center;
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: 1fr 1fr 1fr;
}

footer ul li a {
  color: #ffffff;
  font-size: 3.5rem;
}

footer p {
  color: #ffffff;
  font-size: 1.2rem;
}
/*------------- Main -------------------*/

.about {
  height: auto;
}

main {
  position: relative;
  overflow: hidden;
  z-index: 3;
}

main section {
  display: grid;
  padding: 90px 0;
  width: 70%;
  align-items: center;
  grid-template-columns: 2fr 3fr;
  margin: 50px auto 150px auto;
}

main section h2 {
  color: #dddddd;
  font-size: 4.3rem;
  line-height: 4.2rem;
  padding-right: 10px;
  text-transform: uppercase;
  border-right: 3px solid #dddddd;
}

main section div {
  position: relative;
}

main section div p {
  padding-left: 0px;
  text-align: justify !important;
}

/*------------- Services -------------------*/

div.services {
  display: grid;
  padding: 10px;
  height: auto;
  grid-template-columns: repeat(3,1fr);
}

.services div {
  text-align: center;
  width: 90%;
  background: #ffffff;
  border-radius: 10px;
  margin: 10px auto 50px auto;
  box-shadow: 0px 10px 30px -9px rgba(0,0,0,0.75);
}

.services p.service__icon {
  font-size: 3.4rem;
  text-align: left !important;
  color: rgb(255, 150, 0);
  margin: 0 0 30px 0;
  padding: 30px 0 30px 90px;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  border-bottom: 2px solid rgb(255, 150, 0);
}

.services p.service__title {
  font-size: 3rem;
  margin: 20px;
  padding: 0;
}

.services p.service__title p {
  margin: 20px;
  text-align: justify !important;
}

.services__one {
  margin: 0;
}

.services__two {
  margin: 0;
}

.services__three {
  margin: 0;
}

