  background: url(/react-landing-page/static/media/gallery1.4349d26f.jpeg);
  background-size: 160%;
  grid-row-end: span 2;
  grid-column-end: span 2;
}

.gallery div.gallery__item__two {
  background: url(/react-landing-page/static/media/gallery2.66eb0b4e.jpeg);
  background-size: 160%;
}

.gallery div.gallery__item__three {
  background: url(/react-landing-page/static/media/gallery3.2461fb7d.jpeg);
  background-size: 160%;
}

.gallery div.gallery__item__four {
  background: url(/react-landing-page/static/media/gallery4.c06f0591.jpeg);
  background-size: 160%;
